import React from 'react';
import './MaterialComponent.scss';
import {isMobile, isSamsungBrowser} from '../../../assets/Constants';
import {Slider, TYPE} from '../../../models/Interfaces';
import {IReducers} from '../../../redux/store';
import {connect} from 'react-redux';

interface Props {
    slider: Slider;
    onClick: Function;
    displayState: { summary: boolean };
}

interface State {
    x: number;
    y: number;
    mouseDirection: string;
    text: string;
    hoverWhite: boolean;
    hoverYellow: boolean;
}

class MaterialComponent extends React.Component<Props, State> {


    constructor(props: Props) {
        super(props);
        this.state = {x: 0, y: 0, mouseDirection: "left", text: "", hoverWhite: false, hoverYellow: false};
    }

    handleMouseMove = (e: any, text: string) => {

        this.setState({
            x: e.clientX,
            y: e.clientY,
            text: text
        });
    };

    render() {
        const isNecklace = TYPE.NECKLACE === this.props.slider.material.type;

        return (
            <div className="material-page">
                <div className="grid-container first-card-background bb">
                    {!this.state.hoverWhite && !this.state.hoverYellow &&
                    <div className="main-material-title"> {'Whats your Material?'.toUpperCase()} </div>}

                    <div className={`mini-item ${this.state.hoverWhite ? "cover" : "mini-item-material"}`}
                         onMouseMove={(e) => this.handleMouseMove(e, "YELLOW GOLD")}
                         style={{
                             height: (!isSamsungBrowser && isMobile) ? '78.9vh' : '100%',
                             animation: `${!isSamsungBrowser && isMobile ? 'mymove-chrome' : 'mymove'} 1s`,
                         }}
                         onClick={() => this.props.onClick("YELLOW GOLD")}
                         onMouseEnter={() => this.setState({hoverWhite: true})}
                         onMouseLeave={() => this.setState({hoverWhite: false})}>
                        <img src={this.props.slider.material.yellow}
                             className={`material-image ${isNecklace ? 'necklace' : ''} ${this.state.hoverWhite ? 'selected' : ''}`}
                             alt=""/>
                        {isMobile && !this.props.displayState.summary &&
                        <div className={`type-mobile-material ${this.state.hoverWhite ? 'selected' : ''}`}>
                            YELLOW GOLD
                        </div>}
                    </div>

                    <div className={`mini-item ${this.state.hoverYellow ? "cover" : "mini-item-material"}`}
                         onMouseMove={(e) => this.handleMouseMove(e, "WHITE GOLD")}
                         style={{
                             height: (!isSamsungBrowser && isMobile) ? '78.9vh' : '100%',
                             animation: `${!isSamsungBrowser && isMobile ? 'mymove-chrome' : 'mymove'} 1s`,
                         }}
                         onClick={() => this.props.onClick("WHITE GOLD")}
                         onMouseEnter={() => this.setState({hoverYellow: true})}
                         onMouseLeave={() => this.setState({hoverYellow: false})}>
                        <img src={this.props.slider.material.white}
                             className={`material-image ${isNecklace ? 'necklace' : ''} ${this.state.hoverYellow ? 'selected' : ''}`}
                             alt=""/>

                        {isMobile && !this.props.displayState.summary &&
                        <div className={`type-mobile-material ${this.state.hoverYellow ? 'selected' : ''}`}>
                            WHITE GOLD
                        </div>}
                    </div>


                    {(this.state.hoverWhite || this.state.hoverYellow) &&
                    <div className="mouse-movement"
                         style={{
                             top: this.state.y - 6,
                             left: this.state.mouseDirection === "right" ? this.state.x + 25 : this.state.text.length < 13 ? this.state.x - 200 : this.state.x - 295
                         }}>
                        {this.state.text}
                    </div>}
                </div>
            </div>
        );

    }
}

const mapStateToProps = (state: IReducers) => {
    const {displayState} = state.mainState;
    return {
        displayState
    };
};

const mapDispatchToProps = (dispatch: any) => {
    return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(MaterialComponent);
