export interface MainState {
    slides: Slider[];
    userChoice: any;
    displayState: any;
    price: number;

}

const initState: MainState = {
    slides: [{
        cards: [
            {
                image: isMobile ? mobile_model_earring : model_earring,
                simImage: isMobile ? mobile_model_earring_4sim : model_earring_4sim,
                editImage: isMobile ? mobile_model_earring_4sim_4edit : model_earring_4sim_4edit,
                type: TYPE.EARRING,
                stepsNumber: 999,
            },
            {
                image: isMobile ? mobile_model_necklace : model_necklace,
                simImage: isMobile ? mobile_model_necklace_4sim : model_necklace_4sim,
                editImage: isMobile ? mobile_model_necklace_4sim_4edit : model_necklace_4sim_4edit,
                type: TYPE.NECKLACE,
                stepsNumber: 1,
                rightNecklaceClass: "paint-morse-necklace",
                leftNecklaceClass: "",
            }
        ],
        title: 'WHAT\'S YOUR CATEGORY?', screenType: Types.CATEGORY,
        material: {type: TYPE.NECKLACE, white: white_gold, yellow: yellow_gold}
    },
        {
            cards: [
                {
                    image: isMobile ? mobile_model_earring : model_earring,
                    simImage: isMobile ? mobile_model_earring_4sim : model_earring_4sim,
                    editImage: isMobile ? mobile_model_earring_4sim_4edit : model_earring_4sim_4edit,
                    type: TYPE.SINGLE_EARRING,
                    stepsNumber: 1,
                    rightNecklaceClass: "single-paint-morse",
                    leftNecklaceClass: ""
                },
                {
                    image: isMobile ? mobile_model_earrings_identical : model_earrings_identical,
                    simImage: isMobile ? mobile_model_earrings_identical_4sim : model_earrings_identical_4sim,
                    editImage: isMobile ? mobile_model_earrings_identical_4sim_4edit : model_earrings_identical_4sim_4edit,
                    type: TYPE.IDENTICAL_PAIR,
                    stepsNumber: 1,
                    rightNecklaceClass: "second-paint-morse-right",
                    leftNecklaceClass: "second-paint-morse-left",
                    duplicateMorse: true
                },
                {
                    image: isMobile ? mobile_model_earrings_distinct : model_earrings_distinct,
                    simImage: isMobile ? mobile_model_earrings_distinct_4sim : model_earrings_distinct_4sim,
                    editImage: isMobile ? mobile_model_earrings_distinct_4sim_4edit : model_earrings_distinct_4sim_4edit,
                    type: TYPE.DISTINCT_PAIR,
                    stepsNumber: 2,
                    rightNecklaceClass: "third-paint-morse-right",
                    leftNecklaceClass: "third-paint-morse-left"
                }],
            title: 'WHAT\'S YOUR STYLE?', screenType: Types.STYLE,
            material: {type: TYPE.EARRING, white: white_gold, yellow: yellow_gold}
        }
        // {cards:[{image:model_necklace,type:TYPE.WHITE_GOLD}
        //     ,{image:model6,type:TYPE.YELLOW_GOLD}],title:'WHAT\'S YOUR MATERIAL?',screenType:Types.MATERTIAL}
    ],
    userChoice: {
        category: '',
        style: '',
        material: '',
        chainDetails: {}
    },
    displayState: {
        summary: false,
    },
    price: 0
};

const MainReducer = (state: MainState = initState, action: any) => {
    switch (action.type) {
        case ActionTypes.USER_CHOICE_CATEGORY:
            return {...state, userChoice: {...state.userChoice, category: action.choice.userChoice}};
        case ActionTypes.USER_CHOICE_STYLE:
            return {...state, userChoice: {...state.userChoice, style: action.choice.userChoice}};
        case ActionTypes.USER_CHOICE_MATERIAL:
            return {...state, userChoice: {...state.userChoice, material: action.choice.userChoice}};
        case ActionTypes.USER_CHOICE_TYPE:
            return {...state, userChoice: {...state.userChoice, chainDetails: action.choice.userChoice}};
        case ActionTypes.SET_SUMMARY_STATE:
            state.displayState.summary = action.payload ;
            state.displayState = {...state.displayState} ;
            return {...state};
        case ActionTypes.TOGGLE_SUMMARY:
            state.displayState.summary = !state.displayState.summary ;
            state.displayState = {...state.displayState} ;
            return {...state};
        default:
            return state;
    }

};
export default MainReducer;
