import React from "react";
import {
    BrowserRouter as Router,
    Route,
    Switch
} from 'react-router-dom';
import {AppRoutes} from "./ScreensName";
import HomePage from "./screens/HomePage/HomePage";


export class Main extends React.Component<{}> {

    // constructor(props:any){
    //     super(props)
    //     window.screen.orientation.lock("natural");
    // }

    getAppRoutes() {
        return (
            <Switch>
                <Route path={AppRoutes.MORSE} component={HomePage}/>
                <Route path={AppRoutes.ROOT} component={HomePage}/>
                <Route path={AppRoutes.CATEGORY} component={HomePage}/>
                <Route path={AppRoutes.STYLE} component={HomePage}/>
            </Switch>
        );
    }

    render() {
        return (
            <Router>{this.getAppRoutes()}</Router>
        );
    }
}

