export enum TYPE {
    NECKLACE,
    EARRING,
    WHITE_GOLD,
    YELLOW_GOLD,
    SINGLE_EARRING,
    IDENTICAL_PAIR,
    DISTINCT_PAIR,
    SINGLE_NECKLACE,
    GOLD
}


export enum Types {
    STYLE,
    MATERTIAL,
    CATEGORY
}

