import React from "react";

import './MorsePage.scss';
import {IReducers} from "../../../redux/store";
import {connect} from "react-redux";
import {MorseMenu} from "../../SharedComponents/MorseMenu/MorseMenu";
import {Morse, MorseTextInput} from "../../SharedComponents/MorseTextInput/MorseTextInput";
import circle from '../../../assets/oval.svg';
import {ALPHA_TO_MORSE, isMobile, isSamsungBrowser} from "../../../assets/Constants";
import {userChoiceTypeAction} from "../../../redux/actions/MainAction";

import arrow from '../../../assets/images/arrow-white.svg';
import arrow_grey from '../../../assets/images/arrow-grey.svg';
import arrow_black from '../../../assets/images/arrow-black.svg';
import {Card, ChainDetails} from "../../../models/Interfaces";

interface Props {
    userChoice: any;
    // typeChoice:(type:string)=>void;
    onMorseType: (chainPriceAndWord: any) => void;
    card: Card;
    step: number;
    handleBack: () => void;
    handleNext: () => void;
    finishMobileButton: boolean;
    buyAction: Function;
    selectEventMobile: boolean;
    isAfterBuyPage: boolean;
    resetMorse: boolean;
    isBuyPage: boolean;
    showOnlyMorseChain?: boolean;
    firstChain: any;
    secondChain: any;
    // inputRef:()
}

interface State {
    morseHover: boolean;
    x: number;
    y: number;
    morseMenuOpen: boolean;
    isAfterBuy: boolean;
    firstChain: {
        chain: string;
        chainPriceAndWord: any;
        indexError: number;
    },
    secondChain: {
        chain: string;
        chainPriceAndWord: any;
        indexError: number;
    }

}

export class MorsePage extends React.Component<Props, State> {
    public static defaultProps = {
        showOnlyMorseChain: false
    };

    constructor(props: Props) {
        super(props);

        this.state = {
            morseHover: false,
            x: 0,
            y: 0,
            morseMenuOpen: false,
            isAfterBuy: false,
            firstChain: {
                chain: '',
                indexError: -1,
                chainPriceAndWord: {text: '', length: 0, totalPrice: 0, dashSum: 0, dotSum: 0}
            },
            secondChain: {
                chain: '',
                indexError: -1,
                chainPriceAndWord: {text: '', length: 0, totalPrice: 0, dashSum: 0, dotSum: 0}
            },
        };
    }


    handleMouseMove = (e: any) => {
        this.setState({
            x: e.clientX,
            y: e.clientY
        });
    };

    handleChange = async (chain: string, indexError: number, chainDetails: ChainDetails) => {

        const chainPriceAndWord = {
            text: chain,
            length: chainDetails.length,
            totalPrice: chainDetails.totalPrice,
            dashSum: chainDetails.dashSum,
            dotSum: chainDetails.dotSum
        };

        if (this.props.card.duplicateMorse) {
            await this.setState({
                secondChain: {chain, indexError, chainPriceAndWord},
                firstChain: {chain, indexError, chainPriceAndWord}
            });

        } else {
            if (this.props.step === 1) {
                await this.setState({firstChain: {chain, indexError, chainPriceAndWord}});
            } else {
                await this.setState({secondChain: {chain, indexError, chainPriceAndWord}});
            }

        }
        this.props.onMorseType(this.state);

    };

    handleBuy = () => {
        this.props.buyAction();
        this.setState({isAfterBuy: true});
    };


    componentDidUpdate(prevProps: Props) {
        if (prevProps.selectEventMobile !== this.props.selectEventMobile && isMobile) {
            if (this.props.finishMobileButton) {
                this.props.handleNext();
            }
        }


        if (this.props.resetMorse) {
            this.setState({
                morseHover: false,
                x: 0,
                y: 0,
                morseMenuOpen: false,
                isAfterBuy: false,
                firstChain: {
                    chain: '',
                    indexError: -1,
                    chainPriceAndWord: {text: '', length: 0, totalPrice: 0, dashSum: 0, dotSum: 0}
                },
                secondChain: {
                    chain: '',
                    indexError: -1,
                    chainPriceAndWord: {text: '', length: 0, totalPrice: 0, dashSum: 0, dotSum: 0}
                },
            });
        }
    }

    buttons = () => {

        const enable: boolean = this.state.firstChain.chain.length > 0 && (this.state.firstChain.indexError === -1 && this.state.secondChain.indexError === -1);
        const enable2: boolean = this.props.card.stepsNumber === 2 ? this.state.secondChain.chain.length > 0 && (this.state.secondChain.indexError === -1 && this.state.secondChain.indexError === -1) : true;
        if (this.props.step > this.props.card.stepsNumber)
            return <div className="buy-morse" onClick={this.handleBuy}>
                BUY <img src={arrow_black} className="arrow" alt=""/>
            </div>;
        return <React.Fragment>
            {this.props.step === this.props.card.stepsNumber ?
                <div className={`${enable && enable2 ? 'next' : 'next-disable'}`}
                     onClick={enable && enable2 ? this.props.handleNext : () => {
                     }}>
                    DONE
                    <img src={enable && enable2 ? arrow : arrow_grey} className="arrow" alt=""/>
                </div> :
                <div onClick={enable ? this.props.handleNext : () => {
                }}
                     className={`${enable ? 'next' : 'next-disable'}`}>
                    NEXT EAR
                    <img src={enable ? arrow : arrow_grey} className="arrow" alt=""/>
                </div>}
            {<div
                className="back"
                onClick={this.props.handleBack}>
                <img src={arrow}
                     className="arrow left-arrow" alt=""/> BACK </div>}
        </React.Fragment>;
    };

    inputFoucs(inputRef: any) {
        if (inputRef && !this.state.morseMenuOpen)
            inputRef.focus();
    }

    getTextMargin = () => {
        return 0;
    };


    shiftMargin = () => {
        return 0;   // this.props.isBuyPage && !isMobile ? '25vw' : 0;
    };


    render() {
        const isMorseEdit = (this.props.step === 1) || (this.props.step === 2 && this.props.card?.stepsNumber === 2);

        return (
            <div
                className={`container-morse${this.props.card?.stepsNumber === 2 ? " container-morse-mini-text" : ""}` +
                `${isMorseEdit ? " morse-edit" : ""} ${isMobile ? " mobile" : ""}`}
                style={{
                    height: (!isSamsungBrowser) && isMobile ? '87vh' : '100vh'
                }}>
                {/*<div>{`2222-${String(this.props.step)}-${String(this.props.card.stepsNumber)}-${String(this.props.isAfterBuyPage)}`}</div>*/}
                {this.state.isAfterBuy || this.props.isAfterBuyPage ? <BackToShop/> :
                    <React.Fragment>
                        {!this.props.isBuyPage && !this.props.showOnlyMorseChain &&
                        <div className="question"
                             style={{opacity: this.state.firstChain.chain.length < 1 ? 1 : .4}}>
                            WHAT'S YOUR SECRET?
                        </div>}
                        {!this.props.showOnlyMorseChain && <div className="morse-texts">
                            {this.props.step === 2 && this.props.card?.stepsNumber !== 1 &&
                            <div className='first-morse'>
                                <Morse text={this.state.firstChain.chain} indexError={30}/>
                            </div>}
                            {<MorseTextInput hidePlaceholder={false} inputRef={this.inputFoucs.bind(this)}
                                             className={`morse-input ${this.props.step === 1 ? '' : 'display-none'}`}
                                             onChange={this.handleChange}/>}
                            {this.props.card?.stepsNumber !== 1 &&
                            <MorseTextInput inputRef={this.inputFoucs.bind(this)} hidePlaceholder={true}
                                            className={`morse-input ${this.props.step === 2 ? '' : ' display-none'}`}
                                            onChange={this.handleChange}/>}
                        </div>}

                        <MorseChain
                            card={this.props.card}
                            className={this.props.card.rightNecklaceClass}
                            getTextMargin={this.getTextMargin}
                            shiftMargin={this.shiftMargin}
                            chain={this.props.firstChain ? this.props.firstChain.chain : ""}
                            indexError={this.props.firstChain.indexError}
                            step={this.props.step}
                        />

                        <MorseChain
                            card={this.props.card}
                            className={this.props.card.leftNecklaceClass}
                            getTextMargin={this.getTextMargin}
                            shiftMargin={this.shiftMargin}
                            chain={this.props.secondChain ? this.props.secondChain.chain : ""}
                            indexError={this.props.secondChain.indexError}
                            step={this.props.step}
                        />

                        {!this.props.showOnlyMorseChain && <div className='morse-btns'>{this.buttons()}</div>}
                        {!this.props.showOnlyMorseChain && !this.props.isBuyPage &&
                        <div className={"morse-menu-icon"}
                             onMouseEnter={() => this.setState({morseHover: true})}
                             onMouseLeave={() => this.setState({morseHover: false})}
                             onMouseMove={this.handleMouseMove} onClick={() => {
                            this.setState({morseMenuOpen: true});
                        }}/>}


                        {!this.props.showOnlyMorseChain &&
                        <MorseMenu className={this.state.morseMenuOpen ? '' : 'hide-menu'}
                                   isMobile={isMobile}
                                   onClose={() => {
                                       this.setState({morseMenuOpen: false});
                                   }}/>}
                    </React.Fragment>}


                {this.state.morseHover && <div className="mouse-movement"
                                               style={{top: this.state.y - 6, left: this.state.x - 190}}>
                    MORSE GUIDE
                </div>}
            </div>
        );
    }
}

const DotIcon = React.memo((props: any) => {
    return (
        <svg className='morse-char dot-icon' xmlns="http://www.w3.org/2000/svg" width="4" height="4" viewBox="0 0 4 4">
            <circle cx="4" cy="4" r="2" fillRule="evenodd"
                    transform="translate(-2 -2)"/>
        </svg>

    );
});
const DashIcon = React.memo((props: any) => {
    return (
        <svg className='morse-char dash-svg' width="4" height="12">
            <rect width="4" height="12"/>
        </svg>

    );
});

const mapStateToProps = (state: IReducers) => {
    const {slides, userChoice} = state.mainState;
    // console.log("userChoice",userChoice)
    return {
        slides, userChoice
    };
};

const mapDispatchToProps = (dispatch: any) => {

    return {
        typeChoice: (type: ChainDetails) => dispatch(userChoiceTypeAction(type)),

    };
};


const BackToShop = React.memo((props) => {

    return (
        <div className="back-to-shop-card">
            {!isMobile && <Morse text={"THANK YOU"} indexError={30} additionalClass={"thank-you"} withSpaces={true}/>}
            {isMobile &&
            <>
                <Morse text={"THANK"} indexError={30} additionalClass={"thank-you"}/>
                <Morse text={"YOU"} indexError={30} additionalClass={"thank-you"}/>
            </>}
            <div className="description">YOU WERE REDIRECTED TO THE PAYMENT PAGE TO COMPLETE YOUR ORDER</div>
            <div onClick={() => window.location.reload()} className="back-to-shop">
                CONTINUE SHOPPING
                <img src={arrow} className="arrow" alt=""/>
            </div>
        </div>
    );
});

const MorseChain = React.memo((props: any) => {
    const isMorseEdit = (props.step === 1) || (props.step === 2 && props.card.stepsNumber === 2);

    let calcTop = 0;
    let calcRight = 0;
    let proportionalTop = 0;
    let proportionalRight = 0;
    switch (props.className) {
        case 'single-paint-morse':
            if (isMobile) {
                if (isMorseEdit) {
                    calcTop = 25.5;
                    proportionalRight = -27;
                } else {
                    calcTop = 21;
                    proportionalRight = -4 ;
                }
            } else {
                if (isMorseEdit) {
                    calcRight = 17;
                    proportionalTop = -6;
                } else {
                    calcTop = 37;
                    proportionalRight = -0.5;
                }
            }
            break;
        case 'second-paint-morse-right':
            if (isMobile) {
                if (isMorseEdit) {
                    calcTop = 18.8;
                    proportionalRight = -5.3;
                } else {
                    calcTop = 33;
                    proportionalRight = -16.8;
                }
            } else {
                if (isMorseEdit) {
                    calcRight = 9.3;
                    proportionalTop = -6;
                } else {
                    calcTop = isMobile ? 42 : 41;
                    proportionalRight = isMobile ? 5.4 : 6;
                }
            }
            break;
        case 'second-paint-morse-left':
            if (isMobile) {
                if (isMorseEdit) {
                    calcTop = 18.8;
                    proportionalRight = -32.6;
                } else {
                    calcTop = 33;
                    proportionalRight = 18.4;
                }
            } else {
                if (isMorseEdit) {
                    calcRight = 23.3;
                    proportionalTop = -6;
                } else {
                    calcTop = isMobile ? 42 : 41;
                    proportionalRight = isMobile ? -6.2 : -6.7;
                }
            }
            break;
        case 'third-paint-morse-right':
            if (isMobile) {
                if (isMorseEdit) {
                    calcTop = 25.8;
                    proportionalRight = -27.2;
                } else {
                    calcTop = 15;
                    proportionalRight = 7;
                }
            } else {
                if (isMorseEdit) {
                    calcRight = 17;
                    proportionalTop = -6;
                } else {
                    calcTop = 38;
                    proportionalRight = 1.1;
                }
            }
            break;
        case 'third-paint-morse-left':
            if (isMobile) {
                if (isMorseEdit) {
                    calcTop = 24.7;
                    proportionalRight = -25.4;
                } else {
                    calcTop = 13.9;
                    proportionalRight = 9.2;
                }
            } else {
                if (isMorseEdit) {
                    calcRight = 17.7;
                    proportionalTop = -7;
                } else {
                    calcTop = 37;
                    proportionalRight = 1.6;
                }
            }
            break;
        case 'paint-morse-necklace':
            if (isMobile) {
                if (isMorseEdit) {
                    calcTop = 32;
                    proportionalRight = -26.2;
                } else {
                    calcTop = 34;
                    proportionalRight = 0;
                }
            } else {
                if (isMorseEdit) {
                    calcRight = 16.7;
                    proportionalTop = 8.4;
                } else {
                    calcTop = 62;
                    proportionalRight = 0;
                }
            }
            break;
        default :
            break;
    }

    const desktopRatios = {edit: (1690 / 2670), sim: (1570 / 3070)};
    const mobileRatios = {edit: (2604 / 1500), sim: (2601 / 1500)};
    const ratio = (isMobile ? mobileRatios : desktopRatios)[isMorseEdit ? 'edit' : 'sim'];
    calcTop = !calcTop ? 50 + window.innerWidth * ratio * (proportionalTop) / window.innerHeight : calcTop;
    calcRight = !calcRight ? 50 + window.innerHeight / ratio * (proportionalRight) / window.innerWidth : calcRight;

    return (
        <div className="background-morse-wrapper">
            <div className={`paint-morse ${props.className} ${isMorseEdit ? '' : 'no-morse-edit'} `}
                 style={
                     {
                         marginTop: `${props.getTextMargin()}px`,
                         marginRight: props.shiftMargin(),
                         top: `${calcTop}%`,
                         right: `${calcRight}%`
                     }
                 }>
                <div className='morse-border'>
                    {props.chain.split('').map((c: string, index: any) => {
                        const divKey = `${c}:${index}`;
                        return <div key={divKey}
                                    className={`tasti ${props.indexError === index ? 'red-morse-fill' :
                                        ''} `}>
                            {ALPHA_TO_MORSE[c.toUpperCase()].split('').map((p, iconIndex) => {
                                const iconKey = `${divKey}:${iconIndex}`;
                                if (p === '.')
                                    return <DotIcon key={iconKey} fill={index === props.chain.split('').length - 1}/>;
                                else if (p === '-')
                                    return <DashIcon key={iconKey} fill={index === props.chain.split('').length - 1}/>;
                                else
                                    return '';
                            })}
                        </div>;
                    })}
                </div>
            </div>
        </div>
    );
});


export default connect(mapStateToProps, mapDispatchToProps)(MorsePage);
