// 1. Get products and create lineItems from them 
// https://shopify.github.io/js-buy-sdk/
// Returns all products
export const fetchProducts = (): Promise<Client.Product[]> => {
    return client.product.fetchAll();
}

// 2. Create checkout
export const createCheckout=(): Promise<Cart> => {
    return client.checkout.create();
}

export const fetchCheckout=(checkoutId: string) => {
    return client.checkout.fetch(checkoutId);
}


