import React from 'react';
import './CardComponent.scss';
import {TYPE} from '../../../models/Interfaces';
import {IReducers} from '../../../redux/store';
import {connect} from 'react-redux';
import {isMobile} from '../../../assets/Constants';

interface Props {
    backgroundImage: string;
    mouseText: string;
    onMouseMove: Function;
    onClick: Function;
    selectedCard: boolean;
    isCardSelected: boolean;
    isBuyPage: boolean;
    isAfterBuyPage: boolean;
    isMaterialPage: boolean
    isMorseEdit: boolean
    onMouseEnter: Function;
    onMouseLeave: Function;
    cardType: TYPE;
    buyPage: boolean;
    displayState: { summary: boolean };
}

interface State {
    hover: boolean,
}

class CardComponent extends React.Component<Props, State> {

    getClassName = () => {
        if (this.props.isAfterBuyPage)
            return "";
        if (this.props.isMaterialPage)
            return "full-light";
        return "";
    };


    constructor(props: Props) {
        super(props);
        this.state = {hover: false};
    }

    componentDidUpdate(prevProps: Props) {
    }


    getSelectedCardClass = () => {
        if (this.props.isCardSelected) {
            return this.props.selectedCard ? 'full-width' : 'no-width';
        }
        return '';
    };

    render() {
        const isCenterWoman = this.props.isAfterBuyPage || this.props.isMaterialPage;
        const isNecklaceMove = this.props.isCardSelected && this.props.cardType === TYPE.NECKLACE;

        return (
            <div
                className={`card-wrapper ${this.getSelectedCardClass()} ${this.state.hover ? "cover" : ""} ` +
                `${this.props.isMaterialPage ? "full-light" : ""} ${this.getClassName()}`}
                onClick={this.props.onClick.bind(this)}
                onMouseMove={() => this.props.onMouseMove(this.props.mouseText)}>

                <div className="layer"/>

                {!this.state.hover && !this.props.isBuyPage &&
                <div className='off-line'>
                    <div className="line hidden"/>
                    <div className="line hide-line-mobile"/>
                    <div className="line hide-line-mobile"/>
                    <div className="line"/>
                    <div className="line hide-line-mobile"/>
                    <div className="line hide-line-mobile"/>
                    <div className="line hidden"/>
                </div>}

                <div
                    className={`item-column ${isCenterWoman ? "center-woman" : ""} ` +
                    `${isNecklaceMove ? 'necklace-move' : 'other-move'} ` +
                    `${this.props.isBuyPage ? 'non-dark' : ''} ` +
                    `${this.props.isMorseEdit ? 'morse-edit' : ''} ` +
                    `${this.props.isCardSelected ? '' : 'card-selection'}`}
                    style={{backgroundImage: `url(${this.props.backgroundImage})`,}}
                    onMouseEnter={() => {
                        this.setState({hover: true});
                        this.props.onMouseEnter();
                    }}
                    onMouseLeave={() => {
                        this.setState({hover: false});
                        this.props.onMouseLeave();
                    }}>
                    {!isMobile &&<div className="gradiant"/>}
                </div>

                { !this.props.displayState.summary &&
                <div className={`type-mobile ${this.state.hover ? 'selected' : ''}`}>
                    {this.props.mouseText}
                </div>}
            </div>

        );
    }
}
const mapStateToProps = (state: IReducers) => {
    const {displayState} = state.mainState;
    return {
        displayState
    };
};

const mapDispatchToProps = (dispatch: any) => {
    return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(CardComponent);
