import './ColumnGrid.scss';
import React from 'react';
import CardComponent from '../Card/CardComponent';
import {Card, typeToString, TYPE} from '../../../models/Interfaces';
import MorsePage from '../../screens/MorsePage/MorsePage';
import {RouteComponentProps} from "react-router";
import {isMobile, isSamsungBrowser} from '../../../assets/Constants';
import MaterialComponent from '../Material/MaterialComponent';
import arrow_black from '../../../assets/images/arrow-black.svg';

interface Props {
    slides: any;
    onClick: Function;
    slideIndex: number;
    onMorseType: (chainPriceAndWord: any) => void;
    onBack: Function;
    selectMobileButton: boolean;
    finishMobile: boolean;
    buyAction: Function;
    isAfterBuyPage: boolean;
    selectedBottom: any;
    onEdit: boolean;
    onMaterialClicked: Function;
    firstChain: any;
    secondChain: any;
    isMaterialPage: boolean;

}

interface State {
    x: number;
    y: number;
    mouseText: string;
    mouseDirection: string;
    oldX: number;
    isCardSelected: boolean;
    selectedCard: number;
    step: number;
    currentType: any;
    currentIndex: number;
    finishMobileButton: boolean;
    resetMorse: boolean;
    isMaterialPage: boolean;
    buybtn: boolean;
    hover: boolean;

}

export class SecondColumnGrid extends React.Component<Props & RouteComponentProps, State> {

    unlisten: any;

    constructor(props: Props & RouteComponentProps) {
        super(props);
        this.state = {
            step: 1,
            isCardSelected: false,
            selectedCard: -1,
            x: 0, y: 0, mouseText: '',
            mouseDirection: 'left',
            oldX: 0,
            currentType: TYPE.SINGLE_EARRING,
            currentIndex: -1,
            finishMobileButton: false,
            resetMorse: false,
            isMaterialPage: false, buybtn: false,
            hover: false
        };
    }

    handleMouseMove = (e: any) => {
        this.setState({
            x: e.clientX,
            y: e.clientY
        });
        this.getMouseDirection(e);
    };

    componentDidMount(): void {
        if (this.props.selectedBottom !== null) {
            const slide = this.props.slides[1];
            const index = slide.cards.findIndex((c: Card) => c.type === this.props.selectedBottom);
            if (slide.cards[index])
                this.handleSelect(slide.cards[index], index).then();
        }

        this.unlisten = this.props.history.listen((e, b) => {
            if (b === 'POP') {
                this.backLogic();
            }
        });
    }

    componentWillUnmount(): void {
        this.unlisten();
    }

    backLogic = () => {
        this.props.onBack(this.state.isCardSelected);
        this.props.history.replace('/');
        this.setState({isCardSelected: false, selectedCard: -1, isMaterialPage: false, buybtn: false});
    };

    componentDidUpdate(prevProps: Props) {
        if (prevProps.onEdit !== this.props.onEdit) {
            this.handleBack();
        }
        if (this.props.selectedBottom !== null && prevProps.selectedBottom !== this.props.selectedBottom) {
            const slide = this.props.slides[this.props.selectedBottom === TYPE.NECKLACE ? 0 : 1];
            const index = slide.cards.findIndex((c: Card) => c.type === this.props.selectedBottom);
            if (slide.cards[index])
                this.handleSelect(slide.cards[index], index).then();
            this.setState({resetMorse: true});
            this.setState({resetMorse: false});
        }

        if (prevProps.isMaterialPage !== this.props.isMaterialPage && isMobile) {

            if (this.props.isMaterialPage) {
                this.setState({buybtn: true, isMaterialPage: false});
            }

        }

        if (prevProps.selectMobileButton !== this.props.selectMobileButton && isMobile) {
            this.setState({finishMobileButton: this.props.finishMobile});
            if (this.props.slideIndex === 1 && this.state.currentIndex > -1 && !this.props.finishMobile) {
                this.setState({isCardSelected: true, step: 1, selectedCard: this.state.currentIndex});
            }
        }
    }

    handleSelect = async (c: Card, index: number) => {
        await this.setState({currentIndex: index, currentType: c.type});
        this.props.onClick(typeToString(c.type));
        if (!isMobile) {
            if (this.props.slideIndex === 1) {
                this.setState({isCardSelected: true, step: 1, selectedCard: index});
            }
        }
    };

    getNe = (index: number) => {
        if (this.state.isCardSelected)
            return this.state.selectedCard === index ? 'full-width' : 'no-width';
        return '';
    };

    handleNext = () => {


        if (this.state.step < 3)
            this.setState(prevState => ({...prevState, step: prevState.step + 1}));
        // if(!this.state.isMaterialPage)
        // console.log("this.state.currentType",typeToString(this.state.currentType))
        // console.log("this.state.step",this.state.step)

        if ((this.state.currentType !== TYPE.DISTINCT_PAIR) || (this.state.currentType === TYPE.DISTINCT_PAIR
            && this.state.step === 2)) {
            this.setState({isMaterialPage: true});
        }


    };

    handleBack = () => {
        this.backLogic();
    };

    handleMaterial = (material: string) => {
        this.props.onMaterialClicked(material);
        if (!isMobile) {
            this.setState({buybtn: true, isMaterialPage: false});
        }
    };

    render() {
        const slide = this.props.slides[this.props.slideIndex];
        const currentCard = slide.cards[this.state.selectedCard];
        const isMorseEdit = !!this.state.isCardSelected &&
            ((this.state.step === 1) || (this.state.step === 2 && currentCard?.stepsNumber === 2));
        const isBuy = (this.state.buybtn && !this.props.isAfterBuyPage);

        return (
            <div style={{backgroundColor: '#4c4c4c'}}
                 className={`grid-container second-card-background second-grid ${isBuy ? 'buy-page' : ''} ` +
                 `${this.state.isCardSelected ? 'second-grid-selected' : ''}`}
                 onMouseMove={this.handleMouseMove}>
                {!this.state.isCardSelected && !this.state.isMaterialPage && !this.state.buybtn && !this.state.hover &&
                <div className="main-grid-title"
                     onClick={() => this.handleSelect(this.state.currentType ? this.state.currentType : TYPE.EARRING, 0)}>
                    {this.props.slides[this.props.slideIndex].title}</div>}
                {this.props.slides[this.props.slideIndex].cards.map((c: Card, index: number) => {
                    let image = c.image;
                    if (this.state.isCardSelected) {
                        image = (this.state.step === 1 ||
                            (this.state.step === 2 && slide.cards[this.state.selectedCard].stepsNumber === 2)) ?
                            c.editImage : c.simImage;
                    }
                    return (
                        <div key={index} className={`mini-item ${this.getNe(index)}`} style={{
                            height: (!isSamsungBrowser) && isMobile ? '26.3vh' : isMobile ? '30.65vh' : '100%',
                            animation: `${!isSamsungBrowser && isMobile ? 'mymovesecond-chrome' : 'mymovesecond'} ${index + 0.5}s`
                        }}>
                            <CardComponent
                                mouseText={typeToString(c.type)}
                                selectedCard={true}
                                isCardSelected={this.state.isCardSelected}
                                backgroundImage={image}
                                onMouseMove={(mouseText: string) => {
                                    this.setState({mouseText: mouseText});
                                }}
                                onMouseEnter={() => {
                                    if (!isMobile) this.setState({hover: true});
                                }}
                                onMouseLeave={() => {
                                    if (!isMobile) this.setState({hover: false});
                                }}
                                isBuyPage={isBuy}
                                isAfterBuyPage={this.props.isAfterBuyPage}
                                onClick={() => this.handleSelect(c, index)}
                                isMaterialPage={this.state.isMaterialPage || this.state.buybtn}
                                cardType={c.type}
                                buyPage={this.state.buybtn}
                                isMorseEdit={isMorseEdit}
                            />
                        </div>
                    );
                })}

                {this.state.isMaterialPage &&
                <MaterialComponent slider={this.props.slides[this.props.slideIndex]} onClick={this.handleMaterial}
                />}

                {isBuy && <div className='morse-btns'>
                    <div className="buy-morse" onClick={this.props.buyAction.bind(this)}>
                        BUY <img src={arrow_black} className="arrow" alt=""/>
                    </div>
                </div>}


                {this.state.isCardSelected &&
                <MorsePage selectEventMobile={this.props.selectMobileButton}
                           buyAction={this.props.buyAction.bind(this)}
                           finishMobileButton={this.state.finishMobileButton}
                           handleBack={this.handleBack}
                           handleNext={this.handleNext}
                           step={this.state.step}
                           card={currentCard}
                           isAfterBuyPage={this.props.isAfterBuyPage}
                           resetMorse={this.state.resetMorse}
                           onMorseType={this.props.onMorseType.bind(this)}
                           isBuyPage={this.state.buybtn}
                           firstChain={this.props.firstChain}
                           secondChain={this.props.secondChain}
                           showOnlyMorseChain={this.state.isMaterialPage}
                />}
                {!this.state.isCardSelected && this.state.hover &&
                <div className="mouse-movement"
                     style={{
                         top: this.state.y - 6,
                         left: this.state.mouseDirection === "right" ? this.state.x + 20 : this.state.mouseText.length < 13 ? this.state.x - 115 : this.state.x - 215
                     }}>
                    {this.state.mouseText}
                </div>}
            </div>
        );
    }

    getMouseDirection(e: any) {
        if (this.state.oldX < e.pageX) {
            this.setState({mouseDirection: "left"});
        } else {
            this.setState({mouseDirection: "left"});
        }
        this.setState({oldX: e.pageX});
    }
}
