export const isChrome = /Chrome/.test(navigator.userAgent) && /Google Inc/.test(navigator.vendor);


// var isSafari = /constructor/i.test(window.HTMLElement.toString()) || (function (p) { return p.toString() === "[object SafariRemoteNotification]"; })(!window['safari'] || (typeof safari !== 'undefined' && safari.pushNotification));
// export const isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);

// export const isSafari = (navigator.userAgent.indexOf("Safari") != -1)&&isMobile;
export const isSafari = /Safari/.test(navigator.userAgent) && !/Chrome/.test(navigator.userAgent);

// export const user = navigator.userAgent;

export const isAndroid = /Android/.test(navigator.userAgent);


export const isSamsungBrowser = navigator.userAgent.match(/SamsungBrowser/i) && isMobile;


export const createPopupWin = (pageURL: string, pageTitle: string,
                               popupWinWidth: number, popupWinHeight: number, top: number, left: number) => {
    // var left = (window.screen.width ) ;
    // var top = (window.screen.height ) ;
    window.open(pageURL, pageTitle,
        'resizable=yes, width=' + popupWinWidth
        + ', height=' + popupWinHeight + ', top='
        + top + ', left=' + left);
};

