export const userChoiceCategoryAction = (userChoice: string) => {
    return async (dispatch: Dispatch) => {
        dispatch({
            type: ActionTypes.USER_CHOICE_CATEGORY,
            choice: {userChoice: userChoice}
        });
    };
};

export const userChoiceStyleAction = (userChoice: string) => {
    return async (dispatch: Dispatch) => {
        dispatch({
            type: ActionTypes.USER_CHOICE_STYLE,
            choice: {userChoice: userChoice}
        });
    };
};

export const userChoiceMaterialAction = (userChoice: string) => {
    return async (dispatch: Dispatch) => {
        dispatch({
            type: ActionTypes.USER_CHOICE_MATERIAL,
            choice: {userChoice: userChoice}
        });
    };
};

export const userChoiceTypeAction = (chainDetails: ChainDetails) => {
    return async (dispatch: Dispatch) => {
        dispatch({
            type: ActionTypes.USER_CHOICE_TYPE,
            choice: {userChoice: chainDetails}
        });
    };
};

export const setSummaryStateAction = (aSummaryState: boolean) => {
    return async (dispatch: Dispatch) => {
        dispatch({
            type: ActionTypes.SET_SUMMARY_STATE,
            payload: aSummaryState,
        });
    };
};

export const toggleSummaryAction = () => {
    return async (dispatch: Dispatch) => {
        dispatch({
            type: ActionTypes.TOGGLE_SUMMARY,
        });
    };
};
